.title {
  display: block;
  margin-bottom: 5px;
  font-size: 20px;
  color: #000;
  text-align: center;
}

.input {
  text-align: center;
  display: block;
  width: 200px;
  padding: 5px;
  margin: 0 auto;
  background-color: rgb(232, 235, 240);
  color: #000;
  outline-color: none;
  border-radius: 10px;
}

.button {
  display: block;
  margin: 0 auto;

  margin-top: 25px;
  padding: 5px;
  font-size: 13px;
  font-weight: bold;
  background-color: rgb(131, 161, 212);
  color: #000;
  text-transform: uppercase;

  border-radius: 10px;
}
