.title {
  display: block;
  margin-top: 10px;
  margin-bottom: 15px;
  text-align: center;
  font-size: 16px;
  color: #000;
  text-transform: uppercase;
}
.card {
  display: block;
  width: 360px;
  margin: 0 auto;
  margin-top: 20px;
  padding: 20px;
  background: rgb(198, 199, 201);
  border: 2px dotted #000;
  border-radius: 10px;
}
